<style scoped>
.detail-box {
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 99999999;
  background-color: rgb(89 89 89 / 53%);
  -webkit-filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  -webkit-backdrop-filter: saturate(180%) blur(8px);
  backdrop-filter: saturate(180%) blur(8px);
}
.box-list {
  overflow: hidden;
  margin-bottom: 20px;
}
.detail-warp {
  width: 925px;
  margin: 15px auto;
  background-color: #ffffffc7;
  border-radius: 6px;
  padding: 40px 20px;
  overflow-y: auto;
  max-height: calc(100% - 80px - 110px);
  min-height: 470px;

  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 8px;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.detail-warp::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}

/*  */

::-webkit-scrollbar-track {
  background-color: #f8faff;
  border-radius: 20px;
}

/*  */

::-webkit-scrollbar-thumb {
  background-color: #d9d9d9;
  border-radius: 20px;
}

::-webkit-scrollbar {
  width: 4px;
  height: 8px;
  background-color: #d9d9d9;
  opacity: 0;
  border-radius: 20px;
}
.box-title {
  color: #272727;
  margin-bottom: 5px;
}
.detail-info {
  overflow: hidden;
}
.detail-left,
.detail-right {
  height: 400px;
  float: left;
}
.detail-left {
  width: 45%;
  text-align: center;
}
.detail-left img {
  max-width: 100%;
  max-height: 100%;
  display: inline-block;
}
.detail-right {
  width: 45%;
}
.detail-table {
}
.detail-tr {
  height: 50px;
  line-height: 50px;
  margin-bottom: 5px;
  position: relative;
  background: rgba(255, 255, 255, 0.29);
}
.detail-tr::before {
  content: "";
  width: 0;
  height: 0;
  display: block;
  left: 0;
  top: 0;
  margin: 0 0 0 -50px;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  position: absolute;
  border-left: 50px solid transparent;
  border-bottom: 50px solid rgba(255, 255, 255, 0.11);
  border-bottom-color: rgba(255, 255, 255, 0.29);
}
.detail-tr::after {
  content: "";
  width: 0;
  height: 0;
  display: block;
  right: 0;
  top: 0;
  margin: 0 -50px 0 0;
  -webkit-transition: 0.2s;
  transition: 0.2s;
  position: absolute;
  border-right: 50px solid transparent;
  border-top: 50px solid rgba(255, 255, 255, 0.11);
  border-top-color: rgba(255, 255, 255, 0.29);
}
.detail-td {
  height: 100%;
  float: left;
  box-sizing: border-box;
  padding-left: 18px;
  font-size: 16px;
}
.detail-tr .detail-td:first-child {
  width: 45%;
  font-weight: 600;
  /* background-color: #ffffffbf; */
}
.detail-tr .detail-td:last-child {
  width: 55%;
  /* background-color: #fffffffa; */
}
.detail-title {
  font-size: 24px;
  text-align: center;
  margin-top: 40px;
  margin-bottom: 14px;
  color: #2e2e2e;
  font-weight: 600;
}
</style>



