  mounted: function () {
    const vm = this;
    vm.$nextTick(() => {});
  },
  methods: {
    // 
    getMaxNum(arr) {
      return Math.max.apply(null, arr);
    },
    // 
    arrMinNum(arr) {
      return Math.min.apply(null, arr);
    },
    // 
    getAverageNum(arr) {
      let sum = eval(arr.join("+"));
      return Math.ceil(((sum / arr.length) * 100) / 100);
    },
    // 
    getPriceSection(arr) {
      let arrSort = arr.sort();
      let min = arrSort[0];
      let max = arrSort[arrSort.length - 1];
      let rt = {
        x: [],
        y: [],
      };
      let sec = 30; //
      let minSec = Math.floor(min / sec); //
      let maxSec = Math.ceil(max / sec); //
      for (let i = minSec; i <= maxSec; i++) {
        let num = 0;
        arr.forEach((it, ind, a) => {
          if (it > i * sec && it < (i + 1) * sec) {
            num++;
          }
        });
        rt.x.push(i * sec + "~" + (i + 1) * sec); //
        rt.y.push(num); //
      }
      return rt;
    },
  },
  created() {},
