<style scoped>
.model-toy {
  width: 610px;
  height: 150px;
  border-radius: 10px;
  overflow: hidden;
  margin: 0 10px 10px 0;
  float: left;
}
.toy-warp {
  position: relative;
  background-color: #003455c4;
  background-image: linear-gradient(-90deg, #08a1a1 0, #00366d 100%);
  /* -webkit-box-shadow: 0 2px 10px 3px rgba(0, 58, 124, 0.43);
  box-shadow: 0 2px 10px 3px rgba(0, 58, 124, 0.43);
  -webkit-filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  filter: drop-shadow(0 1px 4px rgba(0, 0, 0, 0.2));
  -webkit-backdrop-filter: saturate(180%) blur(8px);
  backdrop-filter: saturate(180%) blur(8px); */
  min-height: 150px;
  width: 100%;
  height: 100%;
}
.trend-left,
.trend-right {
  float: left;
  height: 100%;
}
.trend-left {
  width: 130px;
  height: 130px;
  border-radius: 4px;
  overflow: hidden;
  margin: 10px 0 0 10px;
}
.trend-left img {
  width: 100%;
  height: 100%;
}
.trend-right {
  position: relative;
  width: calc(100% - 130px - 10px);
  padding-left: 15px;
}
.trend-info {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 2;
}
.trend-echarts {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
  opacity: 0.4;
}
.toy-title {
  color: #fff;
  padding: 20px 0 15px;
  font-size: 22px;
}
.website {
  position: absolute;
  right: 0;
  bottom: 0;
  text-align: center;
  width: 30px;
  height: 30px;
  line-height: 30px;
  color: #ffffffa6;
  cursor: pointer;
  border-radius: 10px 0 0 0;
  z-index: 3;
}
.website:hover {
  color: #fff;
  background-color: #ffffff3b;
}
.sale-date {
  position: absolute;
  right: 10px;
  top: 5px;
  color: #ffffff8f;
  font-size: 14px;
}
.price-item {
  width: 110px;
  display: inline-block;
}
.price-item.yen .price-num {
  color: #03a9f4;
}
.price-item.min .price-num {
  color: #00ff72;
}
.price-item.max .price-num {
  color: #efa500;
}
.price-num {
  color: #fff25c;
  font-size: 26px;
  font-weight: 600;
}
.price-num .unit {
  font-size: 12px;
  color: #00b8d6;
}
.price-name {
  color: #00b8d6;
  font-size: 14px;
}
.toy-des {
  color: #ffffff73;
  padding-top: 10px;
  font-size: 14px;
}
</style>



